import css from './Radio.module.scss'
import classnames from 'classnames'
import { nanoid } from 'nanoid'

function Radio ({
  className,
  label,
  value,
  formRef,
  type = 'checkbox',
  ...props
}) {
  const id = nanoid()

  return (
    <div
      className={classnames(css.wrapper, className, {
        [css.wrapperChecked]: !!value
      })}
    >
      <input
        className={css.radio}
        value={value}
        type={type}
        id={id}
        ref={formRef}
        {...props}
      />
      <label
        htmlFor={id}
        className={css.label}
        dangerouslySetInnerHTML={{ __html: label }}
      />
    </div>
  )
}

export default Radio
