import { useState, useEffect } from 'react'
import css from './Section.module.scss'
import classnames from 'classnames'
import useIntersectionObserver from 'hooks/useIntersectionObserver'

function Section ({
  className,
  TagType = `section`,
  isLight,
  isMedium,
  isWithoutExtension,
  children
}) {
  const [wrapperRef, setWrapperRef] = useState(null)
  const {
    isVisible,
    unsubscribeObserver
  } = useIntersectionObserver({
    element: wrapperRef,
    threshold: 0.05,
    margins: '0% 0% 15% 0%'
  })

  useEffect(() => {
    if (isVisible) {
      unsubscribeObserver()
    }
  }, [isVisible, unsubscribeObserver])

  return (
    <TagType
      className={classnames(css.wrapper, className, {
        [css.wrapperShort]: isWithoutExtension,
        [css.wrapperLight]: isLight,
        [css.wrapperMedium]: isMedium,
        [css.wrapperVisible]: isVisible
      })}
      ref={node => setWrapperRef(node)}
    >
      { children }
    </TagType>
  )
}

export default Section
