import {
  SET_HEADER_HEIGHT,
  SET_LANG,
  TOGGLE_MODAL
} from 'store/actions/actionTypes'

export const setLang = lang => {
  return {
    type: SET_LANG,
    payload: lang
  }
}

export const toggleModal = (content, options) => {
  return {
    type: TOGGLE_MODAL,
    payload: {
      content,
      options
    }
  }
}

export const setHeaderHeight = height => ({
  type: SET_HEADER_HEIGHT,
  payload: height
})
