function IconCircleContacts5 ({
  className
}) {
  return (
    <svg
      className={className}
      viewBox='0 0 40 40'
      fill='none'
      xmlns='http://www.w3.org/2000/svg'
      style={{
        width: `4rem`,
      }}
    >
      <circle cx='20' cy='20' r='18.5' stroke='#FE4D3D' strokeWidth='3'/>
    </svg>
  )
}

export default IconCircleContacts5
