function IconCircleBannerAdaptive3 ({
  className,
}) {
  return (
    <svg
      className={className}
      viewBox='0 0 20 20'
      fill='none'
      xmlns='http://www.w3.org/2000/svg'
      style={{
        width: `2rem`
      }}
    >
      <circle cx='10' cy='10' r='9' stroke='#C4C4C4' strokeWidth='2'/>
    </svg>
  )
}

export default IconCircleBannerAdaptive3
