import css from './TeamAdaptive.module.scss'
import Section from 'components/Templates/Section/Section'
import Heading, { HeadingTypes } from 'components/Atoms/Heading/Heading'
import Description from 'components/Atoms/Description/Description'
import SliderTeam from 'components/Molecules/SliderTeam/SliderTeam'

function TeamAdaptive ({
  className,
  heading,
  description,
  list
}) {
  return (
    <Section
      className={css.wrapper}
      isMedium
    >
      <Heading
        className={css.heading}
        style={HeadingTypes.H3}
        isCapitalized
      >
        { heading }
      </Heading>
      <Description className={css.description}>
        { description }
      </Description>
      <SliderTeam
        list={list}
        className={css.slider}
      />
    </Section>
  )
}

export default TeamAdaptive
