import css from './ContactsForm.module.scss'
import classnames from 'classnames'
import { useForm, Controller } from 'react-hook-form'
import Input from 'components/Atoms/Input/Input'
import Button, { ButtonHeights, ButtonPalettes } from 'components/Atoms/Button/Button'
import Radio from 'components/Atoms/Radio/Radio'

function ContactsForm ({
  className,
  namePlaceholder,
  phonePlaceholder,
  radioLabel,
  buttonLabel
}) {
  const {control, handleSubmit} = useForm()

  function submitForm (data) {
    console.log(data)
  }

  return (
    <form onSubmit={handleSubmit(submitForm)}>
      <div className={classnames(css.form, className)}>
        <Controller
          render={({
            field: {ref, ...props}
          }) => (
            <Input
              className={css.input}
              placeholder={namePlaceholder}
              formRef={ref}
              {...props}
            />
          )}
          name={ `contact-name` }
          control={control}
          rules={{
            required: true
          }}
          defaultValue={ `` }
        />
        <Controller
          render={({
            field: {ref, ...props}
          }) => (
            <Input
              className={css.input}
              placeholder={phonePlaceholder}
              formRef={ref}
              {...props}
            />
          )}
          name={ `contact-phone` }
          control={control}
          rules={{
            required: true
          }}
          defaultValue={ `` }
        />
        <Controller
          render={({
            field: {ref, ...props}
          }) => (
            <Radio
              label={radioLabel}
              className={css.checked}
              formRef={ref}
              {...props}
            />
          )}
          name={ `contact-agree` }
          control={control}
          defaultValue={false}
          rules={{
            required: true
          }}
        />

        <Button
          onClick={() => {}}
          className={css.button}
          height={ButtonHeights.STANDARD}
          palette={ButtonPalettes.DARK}
          type="submit"
        >
          { buttonLabel }
        </Button>
      </div>
    </form>
  )
}

export default ContactsForm
