import css from './ComparisonIconAdaptive.module.scss'

function ComparisonIconAdaptive ({
  IconComponent,
  label
}) {
  return (
    <div className={css.wrapper}>
      <p
        className={css.label}
        dangerouslySetInnerHTML={{ __html: label }}
      />
      {IconComponent && <IconComponent className={css.icon} />}
    </div>
  )
}

export default ComparisonIconAdaptive
