import css from './CirclesBanner.module.scss'
import classnames from 'classnames'
import IconCircleBanner1 from 'assets/icons/circlesBanner/IconCircleBanner1'
import IconCircleBanner2 from 'assets/icons/circlesBanner/IconCircleBanner2'
import IconCircleBanner3 from 'assets/icons/circlesBanner/IconCircleBanner3'
import IconCircleBanner4 from 'assets/icons/circlesBanner/IconCircleBanner4'
import ParallaxLayerCustom from 'components/Templates/ParallaxLayerCustom/ParallaxLayerCustom'
import IconCircleBanner5 from 'assets/icons/circlesBanner/IconCircleBanner5'
