function IconCircleSlider11 ({
  className,
}) {
  return (
    <svg
      className={className}
      viewBox='0 0 11 11'
      fill='none'
      xmlns='http://www.w3.org/2000/svg'
      style={{
        width: `1.1rem`,
        height: `1.1rem`
      }}
    >
      <circle cx='5.5' cy='5.5' r='4' stroke='#B40F00' strokeOpacity='0.2' strokeWidth='3'/>
    </svg>

  )
}

export default IconCircleSlider11
