import { useState } from 'react'
import css from './ContentSlider.module.scss'
import Section from 'components/Templates/Section/Section'
import Container from 'components/Templates/Container/Container'
import Heading from 'components/Atoms/Heading/Heading'
// Import Swiper styles
import SwiperCore, { Navigation, Lazy, Controller, EffectFade } from 'swiper'
import 'swiper/swiper.scss'
import 'swiper/components/navigation/navigation.scss'
import 'swiper/components/lazy/lazy.scss'
import 'swiper/components/effect-fade/effect-fade.scss'
import SliderVideo from 'components/Molecules/SliderVideo/SliderVideo'
import SliderInfo from 'components/Molecules/SliderInfo/SliderInfo'
import Button, { ButtonHeights, ButtonPalettes } from 'components/Atoms/Button/Button'
import CirclesSlider from 'components/Molecules/circles/CirclesSlider/CirclesSlider'
import { isMobile } from 'react-device-detect'
import CirclesSliderAdaptive from 'components/Molecules/circles/CirclesSlider/CirclesSliderAdaptive'
import { scrollToForm } from 'utils'
import IconSpinner from 'assets/icons/IconSpinner'

function ContentSlider ({
  heading,
  description,
  listText,
  listVideo,
  buttonLabels,
}) {
  const [controlledSwiper, setControlledSwiper] = useState(null)

  // install Swiper modules
  SwiperCore.use([Navigation, Lazy, Controller, EffectFade])

  return (
    <Section>
      <Container className={css.wrapper}>
        <Heading
          className={css.heading}
          isCapitalized
          isLightest
        >
          { heading }
        </Heading>
        <p className={css.description}>
          { description }
        </p>
        <div className={css.sliders}>
          <div className={css.video}>
            <IconSpinner className={css.spinner} />
            <SliderVideo
              list={listVideo}
              setControlledSwiper={setControlledSwiper}
            />
          </div>
          <SliderInfo
            className={css.info}
            list={listText}
            controlledSwiper={controlledSwiper}
          />
          <Button
            onClick={scrollToForm}
            className={css.button}
            palette={ButtonPalettes.LIGHT}
            height={ButtonHeights.STANDARD}
          >
            Заказать
          </Button>
        </div>
        {isMobile
          ? <CirclesSliderAdaptive />
          : <CirclesSlider />
        }
      </Container>
    </Section>
  )
}

export default ContentSlider
