import css from './CirclesFeatures.module.scss'
import ParallaxLayerCustom from 'components/Templates/ParallaxLayerCustom/ParallaxLayerCustom'
import classnames from 'classnames'
import IconCircleFeatures1 from 'assets/icons/circlesFeatures/IconCircleFeatures1'
import IconCircleFeatures2 from 'assets/icons/circlesFeatures/IconCircleFeatures2'
import IconCircleFeatures3 from 'assets/icons/circlesFeatures/IconCircleFeatures3'
import IconCircleFeatures4 from 'assets/icons/circlesFeatures/IconCircleFeatures4'
import IconCircleFeatures5 from 'assets/icons/circlesFeatures/IconCircleFeatures5'
import IconCircleFeatures6 from 'assets/icons/circlesFeatures/IconCircleFeatures6'

function CirclesFeatures ({
  className,
}) {
  return (
    <>
      <ParallaxLayerCustom
        className={classnames(css.icon, css.icon1)}
        styleInner={{
          transitionDuration: `0.3s`,
        }}
      >
        <IconCircleFeatures1 />
      </ParallaxLayerCustom>
      <ParallaxLayerCustom
        className={classnames(css.icon, css.icon2)}
        styleInner={{
          transitionDuration: `0.2s`,
        }}
      >
        <IconCircleFeatures2 />
      </ParallaxLayerCustom>
      <ParallaxLayerCustom
        className={classnames(css.icon, css.icon3)}
        styleInner={{
          transitionDuration: `0.1s`,
        }}
      >
        <IconCircleFeatures3 />
      </ParallaxLayerCustom>
      <ParallaxLayerCustom
        className={classnames(css.icon, css.icon4)}
        styleInner={{
          transitionDuration: `0.3s`,
        }}
      >
        <IconCircleFeatures4 />
      </ParallaxLayerCustom>
      <ParallaxLayerCustom
        className={classnames(css.icon, css.icon5)}
      >
        <IconCircleFeatures5 />
      </ParallaxLayerCustom>
      <ParallaxLayerCustom
        className={classnames(css.icon, css.icon6)}
      >
        <IconCircleFeatures6 />
      </ParallaxLayerCustom>
    </>
  )
}

export default CirclesFeatures
