import css from 'components/Molecules/SpecialFeatures/SpecialFeatures.module.scss'
import Section from 'components/Templates/Section/Section'
import Container from 'components/Templates/Container/Container'
import Heading, { HeadingTypes } from 'components/Atoms/Heading/Heading'
import FeatureItem from 'components/Molecules/FeatureItem/FeatureItem'
import CirclesFeatures from 'components/Molecules/circles/CirclesFeatures/CirclesFeatures'
import { isMobile } from 'react-device-detect'

function SpecialFeatures ({
  heading,
  list
}) {
  function renderItems () {
    return list
      .map((item, index) => (
        <FeatureItem
          className={css.item}
          key={index}
          {...item}
        />
      ))
  }

  return (
    <Section className={css.section} isLight>
      <Container className={css.wrapper}>
        <Heading
          className={css.heading}
          style={HeadingTypes.H3}
          isCapitalized
        >
          { heading }
        </Heading>
        <ul className={css.list}>
          { renderItems() }
        </ul>
        {isMobile ? null : <CirclesFeatures />}
      </Container>
    </Section>
  )
}

export default SpecialFeatures
