import css from './Contacts.module.scss'
import Section from 'components/Templates/Section/Section'
import Container from 'components/Templates/Container/Container'
import Heading from 'components/Atoms/Heading/Heading'
import ContactsForm from 'components/Organisms/ContactsForm/ContactsForm'
import CircleContacts from 'components/Molecules/circles/CircleContacts/CircleContacts'
import { isMobile } from 'react-device-detect'
import CircleContactsAdaptive
  from 'components/Molecules/circles/CircleContacts/CircleContactsAdaptive'

function Contacts ({
  className,
  heading,
  description,
  contacts,
  form,
  image
}) {
  return (
    <Section
      isLight
      isWithoutExtension
    >
      <Container className={css.wrapper}>
        <div className={css.intro}>
          <Heading
            isCapitalized
          >
            { heading }
          </Heading>
          <p
            dangerouslySetInnerHTML={{ __html: description }}
            className={css.description}
          />
        </div>
        <div className={css.content}>
          <div className={css.contacts}>
            <span
              className={css.label}
              dangerouslySetInnerHTML={{ __html: contacts.phoneLabel }}
            />
            <span
              className={css.phone}
              dangerouslySetInnerHTML={{ __html: contacts.phone }}
            />
          </div>
          <ContactsForm
            className={css.form}
            {...form}
          />
        </div>
        {isMobile
          ? <CircleContactsAdaptive />
          : <CircleContacts />
        }
      </Container>
    </Section>
  )
}

export default Contacts
