import css from './ComparisonIcon.module.scss'
import classnames from 'classnames'

function ComparisonIcon ({
  className,
  IconComponent,
  label,
  isTextCentered,
  isVisible
}) {
  return (
    <span className={classnames(css.wrapper, className)}>
      <IconComponent className={classnames(css.icon, {
        [css.iconVisible]: isVisible
      })} />
      <span
        className={classnames(css.label, {
          [css.labelCentered]: isTextCentered
        })}
        dangerouslySetInnerHTML={{ __html: label }}
      />
    </span>
  )
}

export default ComparisonIcon
