import css from './TeamItem.module.scss'

function TeamItem ({
  className,
  name,
  role,
  photo,
  Tag = `li`,
  styles = {}
}) {
  return (
    <Tag
      className={className}
      style={{
        gridArea: name.split(' ').join('_')
      }}
    >
      <figure>
        <img
          src={photo}
          className={css.photo}
          alt={ `${name} - ${role}` }
        />
        <figcaption className={css.caption}>
          <span className={css.name}>
            { name }
          </span>
          <span className={css.role}>
            { role }
          </span>
        </figcaption>
      </figure>
    </Tag>
  )
}

export default TeamItem
