import { useEffect, useState } from 'react'
import css from './AftermathItem.module.scss'
import classnames from 'classnames'
import Heading, { HeadingTypes } from 'components/Atoms/Heading/Heading'
import Description from 'components/Atoms/Description/Description'

function AftermathItem ({
  className,
  heading,
  description,
  Icon,
  Tag = `li`,
  isVisible,
  index
}) {
  const [internalVisibility, setInternalVisibility] = useState(false)

  useEffect(() => {
    if (isVisible) {
      setTimeout(() => {
        setInternalVisibility(true)
      }, index * 1500)
    }
  }, [isVisible, index])


  return (
    <Tag
      className={classnames(css.wrapper, className, {
        [css.wrapperVisible]: internalVisibility
      })}
    >
      {Icon &&
        <Icon className={css.icon} />
      }
      <Heading
        className={css.heading}
        tag={HeadingTypes.H3}
        style={HeadingTypes.H4}
        isDarkest
      >
        { heading }
      </Heading>
      <Description className={css.description}>
        { description }
      </Description>
    </Tag>
  )
}

export default AftermathItem
