import css from './Team.module.scss'
import Section from 'components/Templates/Section/Section'
import Container from 'components/Templates/Container/Container'
import Heading, { HeadingTypes } from 'components/Atoms/Heading/Heading'
import Description from 'components/Atoms/Description/Description'
import TeamItem from 'components/Molecules/TeamItem/TeamItem'

function Team ({
  className,
  heading,
  description,
  list
}) {
  // item . item . item
  // . item . item .
  function getGridTemplateAreas (list) {
    const itemsQty = list.length
    const patterns = Math.floor( itemsQty / 5)
    const remainder = itemsQty - patterns * 5
    const remainderRows = remainder === 0
      ? 0
      : remainder > 3
        ? 2
        : 1
    const finalLength = patterns * 2 + remainderRows

    let subarrayIndex = 0
    const structure = new Array(finalLength).fill(``).map(_ => [])
    const lastSubarray = structure[structure.length - 1]

    list.forEach(({name}, index) => {
      if (structure[subarrayIndex].length === 5) subarrayIndex += 1
      structure[subarrayIndex].push(`${name.split(' ').join('_')}`)
      if (structure[subarrayIndex].length === 5) subarrayIndex += 1
      structure[subarrayIndex].push(`.`)
    })

    if (lastSubarray.length < 5) {
      const diff = 5 - lastSubarray.length
      const rest = new Array(diff).fill(`.`)
      structure[structure.length - 1] = [...lastSubarray, ...rest]
    }

    return structure.reduce((total, sub) => total += ` '${sub.join(' ')}'`, ``)
  }

  const gridAreas = getGridTemplateAreas(list)

  function renderItems () {
    return list
      .map((item, index) => (
        <TeamItem
          className={css.item}
          key={index}
          {...item}
        />
      ))
  }

  return (
    <Section isMedium>
      <Container className={css.wrapper}>
        <Heading
          className={css.heading}
          style={HeadingTypes.H3}
          isCapitalized
        >
          { heading }
        </Heading>
        <Description className={css.description}>
          { description }
        </Description>
        <ul
          className={css.list}
          style={{
            gridTemplateAreas: gridAreas}}
        >
          { renderItems() }
        </ul>
      </Container>
    </Section>
  )
}

export default Team
