import { useRef } from 'react'
import css from './SliderInfo.module.scss'
import classnames from 'classnames'
import { Swiper, SwiperSlide } from 'swiper/react'
import Heading, { HeadingTypes } from 'components/Atoms/Heading/Heading'
import Description from 'components/Atoms/Description/Description'
import List from 'components/Atoms/List/List'
import IconSliderArrow from 'assets/icons/slider/IconSliderArrow'
import { isMobile } from 'react-device-detect'

function SliderInfo ({
  className,
  list,
  controlledSwiper
}) {
  const buttonPrevRef = useRef(null)
  const buttonNextRef = useRef(null)

  function renderSlides () {
    return list
      .map(({
        heading,
        description,
        listLabel,
        sublist
      }, index) => (
        <SwiperSlide
          className={css.slide}
          key={index}
        >
          <Heading
            tag={HeadingTypes.H3}
            style={HeadingTypes.H3}
          >
            { heading }
          </Heading>
          <Description className={css.description}>
            { description }
          </Description>
          <span className={css.label}>
            { listLabel }
          </span>
          <List
            className={css.list}
            list={sublist}
            isSmall
          />
        </SwiperSlide>
      ))
  }

  return (
    <div className={classnames(css.wrapper, className)}>
      <Swiper
        slidesPerView={1}
        spaceBetween={50}
        effect={isMobile ? `slide` : `fade`}
        fadeEffect={{
          crossFade: true
        }}
        speed={1000}
        resizeObserver={true}
        controller={{ control: controlledSwiper}}
        onSwiper={swiper => {
          console.log(buttonPrevRef.current)
          swiper.params.navigation.prevEl = buttonPrevRef.current
          swiper.params.navigation.nextEl = buttonNextRef.current
          swiper.navigation.init()
          swiper.navigation.update()
        }}
        navigation={{
          // duplicate prevEl and nextEl to avoid Swiper wrong navigation elements render
          prevEl: buttonPrevRef.current,
          nextEl: buttonNextRef.current,
          disabledClass: css.buttonDisabled
        }}
      >
        { renderSlides() }
      </Swiper>
      <button
        className={css.button}
        type="button"
        ref={buttonPrevRef}
      >
        <span className={css.labelButton}>
          Предыдущая
        </span>
        <IconSliderArrow className={css.icon} />
      </button>
      <button
        className={classnames(css.button, css.buttonNext)}
        type="button"
        ref={buttonNextRef}
      >
        <span className={css.labelButton}>
          Следующая
        </span>
        <IconSliderArrow className={css.icon} />
      </button>
    </div>
  )
}

export default SliderInfo
