import css from './Container.module.scss'
import classnames from 'classnames'

function Container ({
  className,
  style = {},
  getRef,
  children
}) {
  return (
    <div
      className={classnames(css.wrapper, className)}
      style={{...style}}
      ref={getRef}
    >
      { children }
    </div>
  )
}

export default Container
