import { useState } from 'react'
import css from './ComparisonListAdaptive.module.scss'
import ComparisonBlockAdaptive from 'components/Organisms/ComparisonBlock/ComparisonBlockAdaptive'
import { Collapse } from 'react-collapse'
import classnames from 'classnames'
import Button, { ButtonHeights, ButtonPalettes } from 'components/Atoms/Button/Button'
import { scrollToForm } from 'utils'

function ComparisonListAdaptive ({
  className,
  list,
  buttonLabel
}) {
  const [isOpened, setOpenedStatus] = useState(false)

  function renderItems (list) {
    return list
      .map((item, index) => (
        <ComparisonBlockAdaptive
          className={css.item}
          key={index}
          {...item}
        />
      ))
  }

  return (
    <>
      <ul className={className}>
        { renderItems(list.slice(0, 2)) }
      </ul>
      <Collapse
        isOpened={isOpened}
      >
        <ul className={css.listMore}>
          { renderItems(list.slice(2)) }
        </ul>
        {buttonLabel &&
          <Button
            onClick={scrollToForm}
            className={css.buttonScroll}
            palette={ButtonPalettes.DARK}
            height={ButtonHeights.SMALL}
          >
            { buttonLabel }
          </Button>
        }
      </Collapse>
      <button
        className={classnames(css.buttonCollapse, {
          [css.buttonCollapseOpening]: isOpened
        })}
        onClick={() => setOpenedStatus(true)}
        disabled={isOpened}
        type="button"
      >
        Посмотреть остальные этапы
      </button>
    </>
  )
}

export default ComparisonListAdaptive
