function IconSpinner ({
  className,
}) {
  return (
    <svg className={className} viewBox='-1 -1 38 38' xmlns='http://www.w3.org/2000/svg' strokeWidth='2' fill='none' stroke="black">
      <circle strokeOpacity='.5' cx='18' cy='18' r='18'/>
      <path d='M36 18c0-9.94-8.06-18-18-18'>
        <animateTransform
          attributeName='transform'
          type='rotate'
          from='0 18 18'
          to='360 18 18'
          dur='1s'
          repeatCount='indefinite'/>
      </path>
    </svg>
  )
}

export default IconSpinner
