import css from './CirclesComparisonAdaptive.module.scss'
import classnames from 'classnames'
import ParallaxLayerCustom from 'components/Templates/ParallaxLayerCustom/ParallaxLayerCustom'
import IconCircleComparisonAdaptive1
  from 'assets/icons/circlesComparison/IconCircleComparisonAdaptive1'
import IconCircleComparisonAdaptive2
  from 'assets/icons/circlesComparison/IconCircleComparisonAdaptive2'
import IconCircleComparisonAdaptive3
  from 'assets/icons/circlesComparison/IconCircleComparisonAdaptive3'
import IconCircleComparisonAdaptive4
  from 'assets/icons/circlesComparison/IconCircleComparisonAdaptive4'
import IconCircleComparisonAdaptive5
  from 'assets/icons/circlesComparison/IconCircleComparisonAdaptive5'
import IconCircleComparisonAdaptive6
  from 'assets/icons/circlesComparison/IconCircleComparisonAdaptive6'

function CirclesComparisonAdaptive () {
  return (
    <>
      <ParallaxLayerCustom
        className={classnames(css.icon, css.icon1)}
      >
        <IconCircleComparisonAdaptive1 />
      </ParallaxLayerCustom>
      <ParallaxLayerCustom
        className={classnames(css.icon, css.icon2)}
      >
        <IconCircleComparisonAdaptive1 />
      </ParallaxLayerCustom>
      <ParallaxLayerCustom
        className={classnames(css.icon, css.icon3)}
        styleInner={{
          transitionDuration: `0.3s`,
        }}
      >
        <IconCircleComparisonAdaptive2 />
      </ParallaxLayerCustom>
      <ParallaxLayerCustom
        className={classnames(css.icon, css.icon4)}
        styleInner={{
          transitionDuration: `0.2s`,
        }}
      >
        <IconCircleComparisonAdaptive3 />
      </ParallaxLayerCustom>
      <ParallaxLayerCustom
        className={classnames(css.icon, css.icon5)}
        styleInner={{
          transitionDuration: `0.1s`,
        }}
      >
        <IconCircleComparisonAdaptive4 />
      </ParallaxLayerCustom>
      <ParallaxLayerCustom
        className={classnames(css.icon, css.icon6)}
        styleInner={{
          transitionDuration: `0.3s`,
        }}
      >
        <IconCircleComparisonAdaptive5 />
      </ParallaxLayerCustom>
      <ParallaxLayerCustom
        className={classnames(css.icon, css.icon7)}
      >
        <IconCircleComparisonAdaptive6 />
      </ParallaxLayerCustom>
    </>
  )
}

export default CirclesComparisonAdaptive
