import css from './CircleContactsAdaptive.module.scss'
import classnames from 'classnames'
import ParallaxLayerCustom from 'components/Templates/ParallaxLayerCustom/ParallaxLayerCustom'
import IconCircleContactsAdaptive1 from 'assets/icons/contacts/IconCircleContactsAdaptive1'
import IconCircleContactsAdaptive2 from 'assets/icons/contacts/IconCircleContactsAdaptive2'
import IconCircleContactsAdaptive3 from 'assets/icons/contacts/IconCircleContactsAdaptive3'
import IconCircleContactsAdaptive4 from 'assets/icons/contacts/IconCircleContactsAdaptive4'

function CircleContactsAdaptive () {
  return (
    <>
      <ParallaxLayerCustom
        className={classnames(css.icon, css.icon1)}
      >
        <IconCircleContactsAdaptive1 />
      </ParallaxLayerCustom>
      <ParallaxLayerCustom
        className={classnames(css.icon, css.icon2)}
      >
        <IconCircleContactsAdaptive1 />
      </ParallaxLayerCustom>
      <ParallaxLayerCustom
        className={classnames(css.icon, css.icon3)}
        styleInner={{
          transitionDuration: `0.2s`
        }}
      >
        <IconCircleContactsAdaptive2 />
      </ParallaxLayerCustom>
      <ParallaxLayerCustom
        className={classnames(css.icon, css.icon4)}
        styleInner={{
          transitionDuration: `0.3s`
        }}
      >
        <IconCircleContactsAdaptive3 />
      </ParallaxLayerCustom>
      <ParallaxLayerCustom
        className={classnames(css.icon, css.icon5)}
        styleInner={{
          transitionDuration: `0.2s`
        }}
      >
        <IconCircleContactsAdaptive2 />
      </ParallaxLayerCustom>
      <ParallaxLayerCustom
        className={classnames(css.icon, css.icon6)}
      >
        <IconCircleContactsAdaptive4 />
      </ParallaxLayerCustom>
    </>
  )
}

export default CircleContactsAdaptive
