import { Parallax } from 'react-scroll-parallax'
import { isMobile } from 'react-device-detect'

function ParallaxLayerCustom ({
  className,
  styleInner = {},
  children,
  ...props
}) {
  return (
    <Parallax
      className={className}
      y={isMobile ? [`-30px`, `30px`] : [`-40px`, `40px`]}
      styleInner={{
        transitionTimingFunction: `ease-out`,
        ...styleInner
      }}
      {...props}
    >
      { children }
    </Parallax>
  )
}

export default ParallaxLayerCustom
