import css from './ComparisonList.module.scss'
import classnames from 'classnames'
import ComparisonIcon from 'components/Molecules/ComparisonIcon/ComparisonIcon'
import ComparisonBlock from 'components/Organisms/ComparisonBlock/ComparisonBlock'
import ComparisonItemTemplate
  from 'components/Templates/ComparisonItemTemplate/ComparisonItemTemplate'

function ComparisonList ({
  before,
  steps,
  after,
}) {
  const {
    heading: headingBefore,
    IconComponent: IconComponentBefore,
    list: listBefore
  } = before

  const {
    heading: headingAfter,
    IconComponent: IconComponentAfter,
    list: listAfter
  } = after

  function renderItems () {
    return listBefore
      .map((item, index) => (
        <ComparisonBlock
          itemBefore={item}
          itemAfter={listAfter[index]}
          icon={steps[index + 1]}
          isTextCentered={index === listBefore.length - 1}
          key={index}
        />
      ))
  }

  return (
    <>
      <ComparisonItemTemplate className={css.item}>
        <IconComponentBefore className={css.icon} />
        <IconComponentAfter className={classnames(css.icon, css.iconAfter)} />
      </ComparisonItemTemplate>
      <ComparisonItemTemplate className={classnames(css.item, css.itemTypes)}>
        <span className={css.type}>
          { headingBefore }
        </span>
        <ComparisonIcon isVisible {...steps[0]} />
        <span className={classnames(css.type, css.typeAfter)}>
          { headingAfter }
        </span>
      </ComparisonItemTemplate>
      { renderItems() }
    </>
  )
}

export default ComparisonList
