import { useRef } from 'react'
import HeaderLayout from 'components/Molecules/HeaderLayout/HeaderLayout'
import { useDispatch, useSelector } from 'react-redux'
import { headerData } from 'utils/data'
import { isMobile } from 'react-device-detect'
import { setHeaderHeight } from 'store/actions'
import useObserveFontSizeMutation from 'hooks/useObserveFontSizeMutation'

function Header () {
  const header = useRef(null)
  const lang = useSelector(store => store.ui.lang)
  const dispatch = useDispatch()

  useObserveFontSizeMutation({
    callback: () => dispatch(setHeaderHeight(header.current.getBoundingClientRect().height)),
    executionCondition: isMobile
  })

  return (
    <HeaderLayout
      data={headerData[lang][isMobile ? `adaptive` : `desktop` ]}
      getRef={node => header.current = node}
    />
  )
}

export default Header
