import css from './CirclesPackagesAdaptive.module.scss'
import classnames from 'classnames'
import ParallaxLayerCustom from 'components/Templates/ParallaxLayerCustom/ParallaxLayerCustom'
import IconCirclePackagesAdaptive1 from 'assets/icons/packages/IconCirclePackagesAdaptive1'
import IconCirclePackagesAdaptive2 from 'assets/icons/packages/IconCirclePackagesAdaptive2'
import IconCirclePackagesAdaptive3 from 'assets/icons/packages/IconCirclePackagesAdaptive3'
import IconCirclePackagesAdaptive4 from 'assets/icons/packages/IconCirclePackagesAdaptive4'
import IconCirclePackagesAdaptive5 from 'assets/icons/packages/IconCirclePackagesAdaptive5'
import IconCirclePackagesAdaptive6 from 'assets/icons/packages/IconCirclePackagesAdaptive6'

function CirclesPackagesAdaptive () {
  return (
    <>
      <ParallaxLayerCustom
        className={classnames(css.icon, css.icon1)}
      >
        <IconCirclePackagesAdaptive1 />
      </ParallaxLayerCustom>
      <ParallaxLayerCustom
        className={classnames(css.icon, css.icon2)}
      >
        <IconCirclePackagesAdaptive1 />
      </ParallaxLayerCustom>
      <ParallaxLayerCustom
        className={classnames(css.icon, css.icon3)}
        styleInner={{
          transitionDuration: `0.1s`
        }}
      >
        <IconCirclePackagesAdaptive2 />
      </ParallaxLayerCustom>
      <ParallaxLayerCustom
        className={classnames(css.icon, css.icon4)}
        styleInner={{
          transitionDuration: `0.2s`
        }}
      >
        <IconCirclePackagesAdaptive3 />
      </ParallaxLayerCustom>
      <ParallaxLayerCustom
        className={classnames(css.icon, css.icon5)}
      >
        <IconCirclePackagesAdaptive4 />
      </ParallaxLayerCustom>
      <ParallaxLayerCustom
        className={classnames(css.icon, css.icon6)}
        styleInner={{
          transitionDuration: `0.2s`
        }}
      >
        <IconCirclePackagesAdaptive5 />
      </ParallaxLayerCustom>
      <ParallaxLayerCustom
        className={classnames(css.icon, css.icon7)}
        styleInner={{
          transitionDuration: `0.3s`
        }}
      >
        <IconCirclePackagesAdaptive5 />
      </ParallaxLayerCustom>
      <ParallaxLayerCustom
        className={classnames(css.icon, css.icon8)}
        styleInner={{
          transitionDuration: `0.3s`
        }}
      >
        <IconCirclePackagesAdaptive6 />
      </ParallaxLayerCustom>
    </>
  )
}

export default CirclesPackagesAdaptive
