import { useState } from 'react'
import css from './SliderTeam.module.scss'
import classnames from 'classnames'
import SwiperCore, { Navigation } from 'swiper'
import { Swiper, SwiperSlide } from 'swiper/react'
import TeamItem from 'components/Molecules/TeamItem/TeamItem'
import IconArrowTeam from 'assets/icons/IconArrowTeam'

function SliderTeam ({
  className,
  list
}) {
  const [buttonPrev, setButtonPrev] = useState(null)
  const [buttonNext, setButtonNext] = useState(null)
  const [activeIndex, setActiveIndex] = useState(1)

  SwiperCore.use([Navigation])

  function renderSlides () {
    return list
      .map((item, index) => (
        <SwiperSlide
          key={index}
        >
          <TeamItem
            className={css.item}
            {...item}
          />
        </SwiperSlide>
      ))
  }

  return (
    <div className={classnames(css.wrapper, className)}>
      {buttonNext && buttonPrev &&
        <Swiper
          speed={1000}
          spaceBetween={0}
          resizeObserver={true}
          freeMode={{
            sticky: true
          }}
          centeredSlides={true}
          slidesPerView={ `auto` }
          onSwiper={swiper => {
            console.log(swiper)
            swiper.params.navigation.prevEl = buttonPrev
            swiper.params.navigation.nextEl = buttonNext
            swiper.navigation.init()
            swiper.navigation.update()
          }}
          onActiveIndexChange={swiper => {
            console.log(swiper.activeIndex)
            setActiveIndex(swiper.activeIndex + 1)
          }}
        >
          {renderSlides()}
        </Swiper>
      }
      <div className={css.controls}>
        <button
          className={classnames(css.button, css.buttonPrev)}
          type="button"
          ref={node => setButtonPrev(node)}
        >
          Предыдущее фото
          <IconArrowTeam className={css.icon} />
        </button>
        <span className={css.counter}>
          { `${activeIndex} / ${list.length}` }
        </span>
        <button
          className={css.button}
          type="button"
          ref={node => setButtonNext(node)}
        >
          Следующее фото
          <IconArrowTeam className={css.icon} />
        </button>
      </div>
    </div>
  )
}

export default SliderTeam
