import css from './CirclesBannerAdaptive.module.scss'
import classnames from 'classnames'
import ParallaxLayerCustom from 'components/Templates/ParallaxLayerCustom/ParallaxLayerCustom'
import IconCircleBannerAdaptive1 from 'assets/icons/circlesBanner/IconCircleBannerAdaptive1'
import IconCircleBannerAdaptive2 from 'assets/icons/circlesBanner/IconCircleBannerAdaptive2'
import IconCircleBannerAdaptive3 from 'assets/icons/circlesBanner/IconCircleBannerAdaptive3'
import IconCircleBannerAdaptive4 from 'assets/icons/circlesBanner/IconCircleBannerAdaptive4'
import IconCircleBannerAdaptive5 from 'assets/icons/circlesBanner/IconCircleBannerAdaptive5'
import IconCircleBannerAdaptive6 from 'assets/icons/circlesBanner/IconCircleBannerAdaptive6'

function CirclesBannerAdaptive () {
  return (
    <>
      <ParallaxLayerCustom
        className={classnames(css.icon, css.icon1)}
        y={[`0px`, `-35px`]}
      >
        <IconCircleBannerAdaptive1 />
      </ParallaxLayerCustom>
      <ParallaxLayerCustom
        className={classnames(css.icon, css.icon2)}
        y={[`0px`, `-35px`]}
      >
        <IconCircleBannerAdaptive1 />
      </ParallaxLayerCustom>
      <ParallaxLayerCustom
        className={classnames(css.icon, css.icon3)}
        y={[`0px`, `-35px`]}
      >
        <IconCircleBannerAdaptive1 />
      </ParallaxLayerCustom>
      <ParallaxLayerCustom
        className={classnames(css.icon, css.icon4)}
        y={[`0px`, `-35px`]}
        styleInner={{
          transitionDuration: `0.1s`
        }}
      >
        <IconCircleBannerAdaptive2 />
      </ParallaxLayerCustom>
      <ParallaxLayerCustom
        className={classnames(css.icon, css.icon5)}
        y={[`0px`, `-35px`]}
        styleInner={{
          transitionDuration: `0.1s`
        }}
      >
        <IconCircleBannerAdaptive3 />
      </ParallaxLayerCustom>
      <ParallaxLayerCustom
        className={classnames(css.icon, css.icon6)}
        y={[`0px`, `-35px`]}
      >
        <IconCircleBannerAdaptive4 />
      </ParallaxLayerCustom>
      <ParallaxLayerCustom
        className={classnames(css.icon, css.icon7)}
        y={[`0px`, `-35px`]}
        styleInner={{
          transitionDuration: `0.2s`
        }}
      >
        <IconCircleBannerAdaptive5 />
      </ParallaxLayerCustom>
      <ParallaxLayerCustom
        className={classnames(css.icon, css.icon8)}
        y={[`0px`, `-35px`]}
      >
        <IconCircleBannerAdaptive6 />
      </ParallaxLayerCustom>
    </>
  )
}

export default CirclesBannerAdaptive
