import css from './ComparisonToggle.module.scss'
import classnames from 'classnames'
import { ComparisonBlocks } from 'utils/const'

function ComparisonToggle ({
  className,
  value,
  updateValue,
  labelBefore,
  IconBefore,
  labelAfter,
  IconAfter,
  isSelectedBefore
}) {
  return (
    <ul className={classnames(css.list, className, {
      [css.listSelectedBefore]: isSelectedBefore
    })}>
      <li className={css.item}>
        <button
          className={css.button}
          onClick={() => updateValue(ComparisonBlocks.AFTER)}
          type="button"
        >
          <IconAfter className={classnames(css.icon, css.iconAfter)} />
          <span>
            { labelAfter }
          </span>
        </button>
      </li>
      <li className={css.item}>
        <button
          className={classnames(css.button, css.buttonBefore)}
          onClick={() => updateValue(ComparisonBlocks.BEFORE)}
          type="button"
        >
          <IconBefore className={classnames(css.icon, css.iconBefore)} />
          <span>
            { labelBefore }
          </span>
        </button>
      </li>
    </ul>
  )
}

export default ComparisonToggle
