import css from './List.module.scss'
import classnames from 'classnames'

function List ({
  className,
  list,
  isNumbered,
  isSpaceBetween,
  isSmall
}) {
  function renderItems () {
    return list
      .map((item, index) => (
        <li
          className={css.item}
          dangerouslySetInnerHTML={{ __html: `<div>${item}</div>` }}
          key={index}
        />
      ))
  }

  return (
    <ul
      className={classnames(css.list, className, {
        [css.listNumbered]: isNumbered,
        [css.listSpaced]: isSpaceBetween,
        [css.listSmall]: isSmall
      })}
    >
      { renderItems() }
    </ul>
  )
}

export default List
