import { useState, useEffect } from 'react'
import css from './Aftermath.module.scss'
import Section from 'components/Templates/Section/Section'
import Container from 'components/Templates/Container/Container'
import Heading from 'components/Atoms/Heading/Heading'
import Description from 'components/Atoms/Description/Description'
import AftermathItem from 'components/Molecules/AftermathItem/AftermathItem'
import useIntersectionObserver from 'hooks/useIntersectionObserver'
import { isMobile } from 'react-device-detect'

function Aftermath ({
  className,
  heading,
  lead,
  description,
  list
}) {
  const [wrapper, setWrapper] = useState(null)
  const {
    isVisible,
    unsubscribeObserver
  } = useIntersectionObserver({
    element: wrapper,
    margins: "0% 0% 10% 0%",
    triggerCriteria: !isMobile
  })

  useEffect(() => {
    if (isVisible) {
      unsubscribeObserver()
    }
  }, [isVisible, unsubscribeObserver])

  function renderItems () {
    return list
      .map((item, index) => (
        <AftermathItem
          isVisible={isVisible}
          index={index}
          key={index}
          {...item}
        />
      ))
  }

  return (
    <Section isLight >
      <Container className={css.wrapper} getRef={node => setWrapper(node)}>
        <Heading
          className={css.heading}
        >
          { heading }
        </Heading>
        <strong className={css.lead}>
          { lead }
        </strong>
        <Description className={css.description}>
          { description }
        </Description>
        <div className={css.frame}>
          <ul className={css.list}>
            { renderItems() }
          </ul>
        </div>
      </Container>
    </Section>
  )
}

export default Aftermath
