import css from './Exclusive.module.scss'
import Container from 'components/Templates/Container/Container'
import Section from 'components/Templates/Section/Section'
import Heading, { HeadingTypes } from 'components/Atoms/Heading/Heading'
import Description from 'components/Atoms/Description/Description'
import IconArrowExclusive from 'assets/icons/IconArrowExclusive'
import IconArrowExclusiveAdaptive from 'assets/icons/IconArrowExclusiveAdaptive'
import { isMobile } from 'react-device-detect'

function Exclusive ({
  heading,
  slogan,
  descriptionList
}) {
  function renderDescription (list) {
    return list
      .map((item, index) => (
        <Description
          className={css.description}
          key={index}
        >
          { item }
        </Description>
      ))
  }

  return (
    <Section>
      <Container className={css.wrapper}>
        {isMobile
          ? <IconArrowExclusiveAdaptive className={css.icon} />
          : <IconArrowExclusive className={css.icon} />
        }
        <Heading
          className={css.heading}
          tag={HeadingTypes.H2}
          style={HeadingTypes.H2}
          isLightest
          isCapitalized
        >
          { heading }
        </Heading>
        <strong
          dangerouslySetInnerHTML={{ __html: slogan }}
          className={css.slogan}
        />
        { renderDescription(descriptionList) }
      </Container>
    </Section>
  )
}

export default Exclusive
