import { useState, useEffect } from 'react'
import css from './ComparisonBlock.module.scss'
import classnames from 'classnames'
import ComparisonIcon from 'components/Molecules/ComparisonIcon/ComparisonIcon'
import ComparisonItemTemplate
  from 'components/Templates/ComparisonItemTemplate/ComparisonItemTemplate'
import useIntersectionObserver from 'hooks/useIntersectionObserver'

function ComparisonBlock ({
  listBefore,
  listAfter,
  itemBefore,
  itemAfter,
  isTextCentered,
  icon
}) {
  const [wrapperRef, setWrapperRef] = useState(null)

  const {
    isVisible,
    unsubscribeObserver
  } = useIntersectionObserver({
    element: wrapperRef,
    threshold: 0.7
  })

  useEffect(() => {
    if (isVisible) {
      unsubscribeObserver()
    }
  }, [isVisible, unsubscribeObserver])

  return (
    <ComparisonItemTemplate
      className={css.item}
      setRef={node => setWrapperRef(node)}
    >
      <div
        className={classnames(css.description, {
          [css.descriptionVisible]: isVisible
        })}
        dangerouslySetInnerHTML={{ __html: itemBefore }}
      />
      <ComparisonIcon
        className={css.icon}
        isTextCentered={isTextCentered}
        isVisible={isVisible}
        { ...icon }
      />
      <div
        className={classnames(css.description, css.descriptionAfter, {
          [css.descriptionVisible]: isVisible
        })}
        dangerouslySetInnerHTML={{ __html: itemAfter }}
      />
    </ComparisonItemTemplate>
  )
}

export default ComparisonBlock
