function IconCircleSlider2 ({
  className,
}) {
  return (
    <svg
      className={className}
     viewBox='0 0 13 13'
      fill='none'
      xmlns='http://www.w3.org/2000/svg'
      style={{
        width: `1.3rem`,
        height: `1.3rem`
      }}
    >
      <circle cx='6.5' cy='6.5' r='5' stroke='white' strokeOpacity='0.2' strokeWidth='3'/>
    </svg>

  )
}

export default IconCircleSlider2
