import css from './Advantages.module.scss'
import Section from 'components/Templates/Section/Section'
import Container from 'components/Templates/Container/Container'
import Heading, { HeadingTypes } from 'components/Atoms/Heading/Heading'
import List from 'components/Atoms/List/List'
import IconArrowAdvantagesAdaptive from 'assets/icons/IconArrowAdvantagesAdaptive'

function Advantages ({
  heading,
  list
}) {
  return (
    <Section>
      <Container className={css.wrapper}>
        <Heading
          style={HeadingTypes.H3}
          isLightest
          isCapitalized
        >
          { heading }
        </Heading>
        <div className={css.frame}>
          <IconArrowAdvantagesAdaptive className={css.icon} />
          <List
            className={css.list}
            list={list}
            isNumbered
          />
        </div>
      </Container>
    </Section>
  )
}

export default Advantages
