import css from './CirclesSliderAdaptive.module.scss'
import classnames from 'classnames'
import ParallaxLayerCustom from 'components/Templates/ParallaxLayerCustom/ParallaxLayerCustom'
import IconCircleSliderAdaptive1 from 'assets/icons/circlesSlider/IconCircleSliderAdaptive1'
import IconCircleSliderAdaptive2 from 'assets/icons/circlesSlider/IconCircleSliderAdaptive2'
import IconCircleSliderAdaptive3 from 'assets/icons/circlesSlider/IconCircleSliderAdaptive3'
import IconCircleSliderAdaptive4 from 'assets/icons/circlesSlider/IconCircleSliderAdaptive4'
import IconCircleSliderAdaptive5 from 'assets/icons/circlesSlider/IconCircleSliderAdaptive5'

function CirclesSliderAdaptive () {
  return (
    <>
      <ParallaxLayerCustom
        className={classnames(css.icon, css.icon1)}
      >
        <IconCircleSliderAdaptive1 />
      </ParallaxLayerCustom>
      <ParallaxLayerCustom
        className={classnames(css.icon, css.icon2)}
        styleInner={{
          transitionDuration: `0.1s`,
        }}
      >
        <IconCircleSliderAdaptive1 />
      </ParallaxLayerCustom>
      <ParallaxLayerCustom
        className={classnames(css.icon, css.icon3)}
        styleInner={{
          transitionDuration: `0.3s`
        }}
      >
        <IconCircleSliderAdaptive2 />
      </ParallaxLayerCustom>
      <ParallaxLayerCustom
        className={classnames(css.icon, css.icon4)}
      >
        <IconCircleSliderAdaptive3 />
      </ParallaxLayerCustom>
      <ParallaxLayerCustom
        className={classnames(css.icon, css.icon5)}
        styleInner={{
          transitionDuration: `0.2s`
        }}
      >
        <IconCircleSliderAdaptive4 />
      </ParallaxLayerCustom>
      <ParallaxLayerCustom
        className={classnames(css.icon, css.icon6)}
        styleInner={{
          transitionDuration: `0.3s`
        }}
      >
        <IconCircleSliderAdaptive5 />
      </ParallaxLayerCustom>
    </>
  )
}

export default CirclesSliderAdaptive
