function IconCircleContacts3 ({
  className
}) {
  return (
    <svg
      className={className}
      viewBox='0 0 18 18'
      fill='none'
      xmlns='http://www.w3.org/2000/svg'
      style={{
        width: `1.8rem`,
      }}
    >
      <circle cx='9' cy='9' r='7.5' stroke='#FE4D3D' strokeWidth='3'/>
    </svg>
  )
}

export default IconCircleContacts3
