import css from './Comparison.module.scss'
import Section from 'components/Templates/Section/Section'
import Container from 'components/Templates/Container/Container'
import Heading from 'components/Atoms/Heading/Heading'
import ComparisonList from 'components/Organisms/ComparisonList/ComparisonList'
import Button, { ButtonHeights, ButtonPalettes } from 'components/Atoms/Button/Button'
import CirclesComparison from 'components/Molecules/circles/CirclesComparison/CirclesComparison'

function Comparison ({
  heading,
  buttonLabel,
  listHeading,
  before,
  steps,
  after,
}) {
  return (
    <Section isLight>
      <Heading className={css.heading}>
        { heading }
      </Heading>
      <Container className={css.wrapper}>
        <CirclesComparison />
        <ComparisonList
          before={before}
          steps={steps}
          after={after}
        />
        <Button
          onClick={() => {}}
          className={css.button}
          palette={ButtonPalettes.DARK}
          height={ButtonHeights.STANDARD}
        >
          { buttonLabel }
        </Button>
      </Container>
    </Section>
  )
}

export default Comparison
