import { useEffect, lazy, Suspense } from 'react'
import Banner from 'components/Molecules/Banner/Banner'
import { useSelector } from 'react-redux'
import {
  advantagesData,
  aftermathData,
  bannerData,
  comparisonData, contactsData,
  contentSliderData,
  exclusiveData, packagesData,
  specialFeaturesData,
  teamData
} from 'utils/data'
import Exclusive from 'components/Molecules/Exclusive/Exclusive'
import Comparison from 'components/Molecules/Comparison/Comparison'
import SpecialFeatures from 'components/Molecules/SpecialFeatures/SpecialFeatures'
import Advantages from 'components/Molecules/Advantages/Advantages'
import Aftermath from 'components/Molecules/Aftermath/Aftermath'
import Team from 'components/Molecules/Team/Team'
import Packages from 'components/Molecules/Packages/Packages'
import Contacts from 'components/Organisms/Contacts/Contacts'
import { withController } from 'react-scroll-parallax'
import { isMobile } from 'react-device-detect'
import ComparisonAdaptive from 'components/Molecules/Comparison/ComparisonAdaptive'
import TeamAdaptive from 'components/Molecules/Team/TeamAdaptive'
import ContentSlider from 'components/Organisms/ContentSlider/ContentSlider'

const Home = ({parallaxController }) => {
  const lang = useSelector(store => store.ui.lang)
  const contentType = isMobile ? `adaptive` : `desktop`

  useEffect(() => {
    setTimeout(() => {
      parallaxController.update()
    })
  }, [parallaxController])

  const AsyncContentSlider = lazy(() => import(`components/Organisms/ContentSlider/ContentSlider`))

  return (
    <>
      <Banner {...bannerData[lang][contentType]} />
      <Exclusive {...exclusiveData[lang].desktop} />
      {isMobile
        ? <ComparisonAdaptive {...comparisonData[lang].adaptive} />
        : <Comparison {...comparisonData[lang].desktop} />
      }
      <ContentSlider {...contentSliderData[lang].desktop} />
      <SpecialFeatures {...specialFeaturesData[lang][contentType]} />
      <Advantages {...advantagesData[lang][contentType]} />
      <Aftermath {...aftermathData[lang][contentType]} />
      {isMobile
        ? <TeamAdaptive {...teamData[lang].adaptive} />
        : <Team {...teamData[lang].desktop} />
      }
      <Packages {...packagesData[lang].desktop} />
      <Contacts {...contactsData[lang][contentType]} />
    </>
  )
}

export default withController(Home)
