import css from './CirclesComparison.module.scss'
import classnames from 'classnames'
import ParallaxLayerCustom from 'components/Templates/ParallaxLayerCustom/ParallaxLayerCustom'
import IconCircleComparison1 from 'assets/icons/circlesComparison/IconCircleComparison1'
import IconCircleComparison2 from 'assets/icons/circlesComparison/IconCircleComparison2'
import IconCircleComparison3 from 'assets/icons/circlesComparison/IconCircleComparison3'
import IconCircleComparison4 from 'assets/icons/circlesComparison/IconCircleComparison4'
import IconCircleComparison5 from 'assets/icons/circlesComparison/IconCircleComparison5'

function CirclesComparison () {
  return (
    <>
      <ParallaxLayerCustom
        className={classnames(css.icon, css.icon1)}
        styleInner={{
          transitionDuration: `0.3s`,
        }}
      >
        <IconCircleComparison1 />
      </ParallaxLayerCustom>
      <ParallaxLayerCustom
        className={classnames(css.icon, css.icon2)}
        styleInner={{
          transitionDuration: `0.3s`,
        }}
      >
        <IconCircleComparison1 />
      </ParallaxLayerCustom>
      <ParallaxLayerCustom
        className={classnames(css.icon, css.icon3)}
        styleInner={{
          transitionDuration: `0.2s`,
        }}
      >
        <IconCircleComparison2 />
      </ParallaxLayerCustom>
      <ParallaxLayerCustom
        className={classnames(css.icon, css.icon4)}
        styleInner={{
          transitionDuration: `0.2s`,
        }}
      >
        <IconCircleComparison3 />
      </ParallaxLayerCustom>
      <ParallaxLayerCustom
        className={classnames(css.icon, css.icon5)}
        styleInner={{
          transitionDuration: `0.1s`,
        }}
      >
        <IconCircleComparison4 />
      </ParallaxLayerCustom>
      <ParallaxLayerCustom
        className={classnames(css.icon, css.icon6)}
      >
        <IconCircleComparison5 />
      </ParallaxLayerCustom>
    </>
  )
}

export default CirclesComparison
