function IconCircleSliderAdaptive4 ({
  className,
}) {
  return (
    <svg
      className={className}
      viewBox='0 0 15 15'
      fill='none'
      xmlns='http://www.w3.org/2000/svg'
      style={{
        width: `1.5rem`
      }}
    >
      <circle cx='7.5' cy='7.5' r='6.5' stroke='white' strokeOpacity='0.2' strokeWidth='2'/>
    </svg>
  )
}

export default IconCircleSliderAdaptive4
