import { useState, useEffect, useRef } from 'react'
import css from './Banner.module.scss'
import classnames from 'classnames'
import Container from 'components/Templates/Container/Container'
import Heading, { HeadingTypes } from 'components/Atoms/Heading/Heading'
import Button, { ButtonHeights, ButtonPalettes } from 'components/Atoms/Button/Button'
import IconArrowBanner from 'assets/icons/IconArrowBanner'
import siteVideo from 'assets/videos/main.mp4'
import CirclesBanner from 'components/Molecules/circles/CirclesBanner/CirclesBanner'
import { isMobile } from 'react-device-detect'
import { useSelector } from 'react-redux'
import IconArrowDown from 'assets/icons/IconArrowDown'
import { scrollToForm } from 'utils'
import CirclesBannerAdaptive from 'components/Molecules/circles/CirclesBanner/CirclesBannerAdaptive'

function Banner ({
  heading,
  description,
  buttonLabel,
  nextLabel
}) {
  const bannerRef = useRef(null)
  const [video, setVideo] = useState(null)
  const [isLoaded, setLoaded] = useState(false)
  const headerHeight = useSelector(store => store.ui.headerHeight)

  function scrollToNextBlock () {
    window.scrollTo({
      top: headerHeight + bannerRef.current.getBoundingClientRect().height,
      behavior: `smooth`
    })
  }

  useEffect(() => {
    if (video) {
      video.addEventListener(`loadeddata`, () => {
        setLoaded(true)
        video.defaultMuted = true
        video.muted = true
      })
    }
  }, [video])

  return (
    <section
      className={css.section}
      ref={bannerRef}
    >
      <Container
        className={classnames(css.wrapper, {
          [css.wrapperShadow]: isLoaded
        })}
        style={{
          minHeight: isMobile ? `calc(100vh - ${headerHeight}px)` : `auto`
        }}
      >
        {isMobile
          ? <CirclesBannerAdaptive />
          : <CirclesBanner />}
        <div className={css.frame}>
          <video
            className={css.video}
            src={siteVideo}
            autoPlay
            loop
            playsInline
            ref={node => setVideo(node)}
          />
        </div>
        <p className={css.labelNext}>
          <span>
            { nextLabel }
          </span>
          <IconArrowBanner className={css.arrow} />
        </p>
        <div className={css.textBlock}>
          <Heading
            className={css.heading}
            tag={HeadingTypes.H1}
            style={HeadingTypes.H1}
            isCapitalized
          >
            { heading }
          </Heading>
          <p
            dangerouslySetInnerHTML={{ __html: description }}
            className={css.description}
          />
          <Button
            onClick={scrollToForm}
            className={css.button}
            palette={ButtonPalettes.DARK}
            height={ButtonHeights.STANDARD}
          >
            { buttonLabel }
          </Button>
        </div>
        {isMobile &&
          <>
            <Button
              onClick={scrollToForm}
              className={css.buttonAdaptive}
              palette={ButtonPalettes.DARK}
              height={ButtonHeights.STANDARD}
            >
              { buttonLabel }
            </Button>
            <button
              onClick={scrollToNextBlock}
              className={css.buttonScroll}
              type="button"
            >
              Скролл к следующему блоку
              <IconArrowDown className={css.iconArrow} />
            </button>
          </>
        }
      </Container>
    </section>
  )
}

export default Banner
