import css from './Button.module.scss'
import classnames from 'classnames'

export const ButtonHeights = {
  STANDARD: `STANDARD`,
  SMALL: `SMALL`
}

export const ButtonPalettes = {
  DARK: `DARK`,
  LIGHT: `LIGHT`,
  NONE: `NONE`
}

function Button ({
  className,
  type = `button`,
  height,
  palette = ButtonPalettes.NONE,
  children,
  ...props
}) {
  return (
    <button
      className={classnames(css.button, className, {
        [css.buttonStandard]: height === ButtonHeights.STANDARD,
        [css.buttonSmall]: height === ButtonHeights.SMALL,
        [css.buttonShaped]: palette !== ButtonPalettes.NONE,
        [css.buttonDark]: palette === ButtonPalettes.DARK,
        [css.buttonLight]: palette === ButtonPalettes.LIGHT,
      })}
      type={type}
      {...props}
    >
      { children }
    </button>
  )
}

export default Button
