import css from './Packages.module.scss'
import Section from 'components/Templates/Section/Section'
import Container from 'components/Templates/Container/Container'
import Heading from 'components/Atoms/Heading/Heading'
import Description from 'components/Atoms/Description/Description'
import Button, { ButtonHeights, ButtonPalettes } from 'components/Atoms/Button/Button'
import PackageItem from 'components/Molecules/PackageItem/PackageItem'
import CirclesPackages from 'components/Molecules/circles/CirclesPackages/CirclesPackages'
import { isMobile } from 'react-device-detect'
import CirclesPackagesAdaptive
  from 'components/Molecules/circles/CirclesPackages/CirclesPackagesAdaptive'
import { scrollToForm } from 'utils'

function Packages ({
  className,
  heading,
  descriptor,
  buttonLabel,
  priceLabel,
  list
}) {
  function renderItems () {
    return list
      .map((item, index) => (
        <PackageItem
          priceLabel={priceLabel}
          key={index}
          {...item}
        />
      ))
  }

  return (
    <Section>
      <Container className={css.wrapper}>
        <div className={css.intro}>
          <Heading
            className={css.heading}
            isCapitalized
            isLightest
          >
            { heading }
          </Heading>
          <Description
            className={css.description}
            isLightest
          >
            { descriptor }
          </Description>
        </div>
        <div className={css.frame}>
          <ul className={css.list}>
            { renderItems() }
          </ul>
        </div>
        <Button
          onClick={scrollToForm}
          className={css.button}
          palette={ButtonPalettes.LIGHT}
          height={ButtonHeights.SMALL}
        >
          { buttonLabel }
        </Button>
        {isMobile
          ? <CirclesPackagesAdaptive />
          : <CirclesPackages />}
      </Container>
    </Section>
  )
}

export default Packages
