import css from './ComparisonBlockAdaptive.module.scss'
import classnames from 'classnames'
import ComparisonIconAdaptive from 'components/Molecules/ComparisonIcon/ComparisonIconAdaptive'

function ComparisonBlockAdaptive ({
  className,
  description,
  Tag = `li`,
  ...props
}) {
  return (
    <Tag className={classnames(css.wrapper, className)}>
      <ComparisonIconAdaptive
        {...props}
      />
      <div
        className={css.description}
        dangerouslySetInnerHTML={{ __html: description }}
      />
    </Tag>
  )
}

export default ComparisonBlockAdaptive
