import { useState } from 'react'
import css from './ComparisonAdaptive.module.scss'
import Section from 'components/Templates/Section/Section'
import Container from 'components/Templates/Container/Container'
import Heading from 'components/Atoms/Heading/Heading'
import ComparisonToggle from 'components/Molecules/Comparison/ComparisonToggle'
import { ComparisonBlocks } from 'utils/const'
import ComparisonListAdaptive from 'components/Organisms/ComparisonList/ComparisonListAdaptive'
import CirclesComparisonAdaptive
  from 'components/Molecules/circles/CirclesComparison/CirclesComparisonAdaptive'

function ComparisonAdaptive ({
  heading,
  before,
  after,
}) {
  const [tab, setTab] = useState(ComparisonBlocks.AFTER)

  return (
    <Section isLight>
      <Heading className={css.heading}>
        { heading }
      </Heading>
      <Container className={css.wrapper}>
        <ComparisonToggle
          value={tab}
          updateValue={setTab}
          labelBefore={before.heading}
          IconBefore={before.IconComponent}
          labelAfter={after.heading}
          IconAfter={after.IconComponent}
          isSelectedBefore={tab === ComparisonBlocks.BEFORE}
        />
        <ComparisonListAdaptive
          className={css.list}
          list={tab === ComparisonBlocks.AFTER ? after.list : before.list}
          buttonLabel={tab === ComparisonBlocks.AFTER ? after.buttonLabel : null}
        />
        <CirclesComparisonAdaptive />
      </Container>
    </Section>
  )
}

export default ComparisonAdaptive
